#contect{
    background-image: url(../public/imege/night-time-4k-desktop-high-resolution-wallpaper-preview.jpg);
    background-size: cover;
    background-repeat: no-repeat;
     color: azure;
     font-size: larger;
     font-family: fantasy;
}

iframe{
    height:70vh;
    width:160vh;
    border: 5px dotted white ;
    border-radius: 30px;
    box-shadow: 0px 5px 12px rgba(243, 238, 238, 0.5);
    transition: 1s;
    position: absolute;
    margin-top: 3%;
    margin-left: 10%;
}
#map{
    /* background-image: linear-gradient(160deg,white,gray); */
    background-image: linear-gradient(100deg, rgb(26, 24, 25),rgb(158, 152, 152));
   
}
#map h1{
    position: static;
    padding-top: 2%;
    color: beige;
}
#map h1:hover{
    color: lightgreen;
}
iframe:hover{
    box-shadow: 0px -5px -12px rgba(14, 13, 13, 0.5);
        transition: 1s;
}
.main-box{width: 70%; 
           margin: auto;
           position: static;
           padding-top: 5%;       
}

.row{padding-top: 20px;}

.main-box h1{
    color: rgb(163, 49, 97);
    font-family: Rubik Microbe;   
}


@media screen and (max-width:576px) {
    #contect{
        background-image: url(../public/imege/demo.jpg);
        background-size: cover;
        background-repeat: no-repeat;
         color: azure;
         font-size: larger;
         font-family: fantasy;
    }
    iframe{
        height:75vh;
        width:260px;
        border: 5px dotted white ;
        border-radius: 30px;
        box-shadow: 0px 5px 12px rgba(241, 235, 235, 0.5);
        transition: 1s;
        position: absolute;
        margin-top: 15%;
        margin-left: 15%;
    }
    #map{
    background-image: linear-gradient(100deg, rgb(26, 24, 25),rgb(158, 152, 152));
     height: 600px;   
    
    }
    #map h1{
        color: beige;
        
    }
    #map h1:hover{
        color: rgb(46, 201, 46);
        
    }
    iframe:hover{
        box-shadow: 0px -5px -12px rgba(236, 230, 230, 0.5);
            transition: 1s;
    }

    







}