
*{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
    font-family: "Sofia", sans-serif;
    margin-bottom: 0%;

}

.portfolio{
    color:white;
}

.nav-item{
    position: relative;
    margin-left: 160px;
    
    background-color: aliceblue;
    border-radius: 50px;
    background: linear-gradient(100deg, gray,white);
    
}
.nav-item:hover{
    background: linear-gradient(100deg, gray,rgb(19, 14, 14));

}



 

@media screen and (max-width:576px) {
    .nav-item{
        position: relative;
        border-radius: 10px;
        margin-left: 10px;
        box-sizing: content-box;
        text-align: center;
        font-style: italic;
    }
    .nav-item:hover{
        background: linear-gradient(100deg, gray,rgb(19, 14, 14));

    } 
    .pro{
        text-align: center;
    }

}