#about{
    display: flex;
    justify-content: center;
    align-items: center;

background-image: url(../public/imege/wall.jpg);
background-repeat: no-repeat;
background-size: cover;


}
strong{
    color: darksalmon;
}
.circle{
    position: relative;
    
    width: 430px;
    height: 430px;
    background-image: url(../public/imege/blue-bg.jpg);
    background-repeat: no-repeat;
    background-size: 100%;
    background-color:lavender;
    
    border-radius: 50%;
    box-shadow: 0px 5px 12px rgba(14, 13, 13, 0.5);
    transition: 1s;
}
.circle img{
    width: 280px;
    height: 300px;
    align-items: center;
    border-radius: 50px;
    position: absolute;
    left: 17%;
    top: 8%;
    margin-bottom: 5%;
    border-radius: 50%;
    filter: drop-shadow(0px 20px 15px white);
    transition: 1s;

}

.circle:hover{
    width:700px ;
    height: 500px;
    /* border: 5px double rgb(199, 199, 80); */

    border-radius: 10px;
    transition: 1s;
    border: 5px solid white;

}
.circle:hover img{
    position: absolute;
    top: 5%;
    left: 5%;
    border: 5px double white;

    transition: 1s;
}
.content h1{
    /* font-size: small; */
    font-style: italic;
    color:palevioletred;

}

.circle:hover .content{
    list-style: none;
    position: absolute;
    left: 30%;
    top: 15%;
    right: 0%;
    color: rgb(235, 235, 243);
    /* text-align: justify; */

    opacity: 1;
    transition: 1s;
}
 .circle .content{
    text-transform: capitalize;
    align-items: baseline;
    opacity: 0;
    /* text-align: justify; */

}
li{
    transition: 2s;
    font-family: "Sofia", sans-serif;

    
font-size: 18px;
}
.circle .icon{
   display: flex;
   align-items: center;
   justify-content: center;
   position: relative;
   opacity: 0;
   font-size: medium;
   font-style: oblique;
   color: magenta;

}
.circle:hover .icon{
    position: static;
    padding-top:60%;
    opacity: 1;
    transition: 1s;
}


