.footer{
    position: relative;
    margin-top: 0.2%;
    
    height: 80px;
    width: 100%;
   background-image: linear-gradient(100deg, whitesmoke,rgb(199, 196, 196))
    
    /* display: flex; */

}

.details p{
    position: static;
    margin-left: 70%;
    margin-top: -1%;
/* margin-bottom: -6%; */

}
.copy-right{
    color: grey;
    
    margin-left: 3%;
    margin-top: -5%;
}
 
@media screen and (max-width:576px) {

    .footer{
        margin-top: 1.5%;
        margin-bottom: 0%;
        height: 100px;
        width: 100%;
       background-image: linear-gradient(150deg,  whitesmoke,rgb(199, 196, 196));
        
       
    
    }
    
    .details p{
        position: static;
        margin-left: 30%;
        margin-top: -2%;
        font-size: 12px;
    color:black;
    }
    .details p:hover{
        color: rgb(45, 105, 105);
    }

    .copy-right{
        color: grey;
        font-size: 12px;
        margin-left: 20%;
        margin-top: -1%;
    }
}