section{
    width: 100%;
    min-height: 100vh;
}

#home{
    /* position: relative; */
    width: 100%;
    height: 100vh;
    background-image: url(../public/imege/photography-4.jpg) ;
    /* filter: blur(50px); */
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    color: white;
    align-items: center;
    flex-direction: column;
  
    
}

#home p{
    position: absolute;
    top: 50%;
    font-size: 80px;
    opacity: 0;
    animation: text 3s ease-in-out;
    font-style: italic;

}
span{
    color: darkturquoise;
    font-family: fantasy;
  }
#home p:nth-child(1){
    animation-delay: 1s;
}

#home p:nth-child(2){
    animation-delay: 5s;
}
#home p:nth-child(3){
    animation-delay: 10s;
}
#home p:nth-child(4){
    animation-delay: 13s;
}
#home p:nth-child(5){
    animation-delay: 18s;
}
#home p:nth-child(6){
    animation-delay: 23s;
}

@keyframes text
{
    50%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}
@media screen and (max-width:576px){
    #home{
        width: 100%;
        height: 100vh;
        background-image: url("../public/imege/photography-4.jpg");
        filter: blur(0px);
        background-repeat: no-repeat;
        background-size: cover;
        display: flex;
        color: white;
        align-items: center;
        flex-direction: column;
        overflow-x: hidden;
        
    }
    #home p{
        position: absolute;
        top: 30%;
        left: 12%;
        font-size: 55px;
        opacity: 0;
        animation: text 4s ease-in-out;
        font-family:sans-serif;
    
    }
    span{
        color: yellow;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        
      }
    #home p:nth-child(1){
        animation-delay: 0.5s;
    }
    
    #home p:nth-child(2){
        animation-delay: 5s;
    }
    #home p:nth-child(3){
        animation-delay: 10s;
    }
    #home p:nth-child(4){
        animation-delay: 15s;
    }
    #home p:nth-child(5){
        animation-delay: 20s;
    }
    #home p:nth-child(6){
        animation-delay: 25s;
    }
    
    @keyframes text
    {
        50%{
            opacity: 1;
        }
        100%{
            opacity: 0;
        }
    }
    
}